<template>
  <v-dialog class="edit-name-component" v-model="dialog.show" max-width="290">
    <v-card>
      <v-card-title class="headline">Edit Item name</v-card-title>

      <v-card-text class="pb-2">
        <v-container class="pt-0">
          <v-row class="align-center">
            <v-col class="pa-0 my-2 text-center">
              <p class="text grey--text">List Name</p>
            </v-col>
            <v-col class="px-0" v-if="dialog.item">
              <v-text-field
                autofocus
                @focus="$event.target.select()"
                ref="qInput"
                label
                placeholder
                color="pink"
                v-model="dialog.item.text"
              ></v-text-field>
            </v-col>
          </v-row>

          <v-row v-if="dialog.item">
            <div v-for="item in colors" :key="item" @click="$emit('setColor', item)">
              <color-picker :rounded="true" :color="item" :selected="dialog.item.color == item" />
            </div>
          </v-row>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>

        <v-btn color="green darken-1" text @click="dialog.show = false">CANCEL</v-btn>

        <v-btn color="green darken-1" text @click="$emit('save')">SAVE</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import ColorPicker from "@/components/ColorPicker";

export default {
  name: "EditOption",

  props: ["dialog"],

  components: { ColorPicker },

  data: () => ({
    colors: [
      "#f44336",
      "#e91e63",
      "#9c27b0",
      "#673ab7",
      "#3f51b5",
      "#2196f3",
      "#00bcd4",
      "#009688",
      "#4caf50",
      "#ff5722",
      "#795548",
      "#9e9e9e",
      "#607d8b"
    ]
  }),

  created() {
    let colorIndex = this.dialog.item.colorIndex;

    if (colorIndex !== undefined) {
      if (colorIndex >= this.colors.length) {
        colorIndex = colorIndex % this.colors.length;
      }

      this.dialog.item.color = this.colors[colorIndex];
      delete this.dialog.item.colorIndex;
    }
  }
};
</script>
